@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/dm-sans-v5-latin-regular.eot');
  src: local('DM Sans Regular'), local('DMSans-Regular'),
    url('../fonts/dm-sans-v5-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    url('../fonts/dm-sans-v5-latin-regular.woff2') format('woff2'),
    url('../fonts/dm-sans-v5-latin-regular.woff') format('woff'),
    url('../fonts/dm-sans-v5-latin-regular.ttf') format('truetype'),
    url('../fonts/dm-sans-v5-latin-regular.svg#DMSans') format('svg');
}

